#loading_card {
  width: 300px;
  background: #fff;
  border-radius: 10px;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.1);
}
#loading_card_content {
  width: calc(100% - 20px);
  padding: 20px;
}

#loading_card_image,
#loading_card_content > h2,
#loading_card_content > p {
  background: linear-gradient(110deg, #ececec 8%, #f5f5f5 18%, #ececec 33%);
  border-radius: 5px;
  background-size: 200% 100%;
  animation: 1s shine linear infinite;
}
#loading_card_image {
  height: 200px;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 0;
}
#loading_card_content {
  padding: 20px;
}
#loading_card_content > h2 {
  height: 30px;
  margin-block: 5px;
}
#loading_card_content > p {
  height: 70px;
  width: 80%;
  margin-block: 5px;
}

@keyframes shine {
  to {
    background-position-x: -200%;
  }
}

@media (max-width: 550px) {
  #loading_card {
    width: 90%;
  }
}
