#admin_page {
  display: flex;
  padding-block: 7vmin;
}

#inputs {
  height: fit-content;
  padding-block: 100px;
}

#inputs {
  width: 35%;
}

#inputs {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  padding-inline-start: 20px;
  gap: 20px;
}

#inputs > input,
textarea {
  width: 70%;
  height: 40px;
  padding: 8px 15px;
  outline: none;
  border: none;
  border-radius: 8px;
  /* background-color: transparent; */

  font-size: 14px;
  font-family: sans-serif;
  color: black;
}

#inputs > textarea {
  height: auto;
  padding-top: 10px;
}

/* FOR PREVIEW SECTION */

#preview {
  display: flex;
  align-items: center;
  justify-content: flex-start;
  height: fit-content;
  width: 60%;
  margin-inline: auto;
  margin-block-start: 5px;
  margin-block-end: 30px;
  box-shadow: 3px 3px 10px 3px gray;
  border-radius: 10px 10px;
}

#preview_h1 {
  padding-block: 20px;
  text-align: center;
  font-family: 'Courier New', Courier, monospace;
}

#preview_image_h1 {
  width: 55%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;

  text-align: center;
}

#preview_image {
  height: fit-content;
  width: fit-content;
  position: relative;
}

#preview_image > img {
  height: 100%;
  width: 100%;
  border-radius: 10px 10px 10px 10px;
  object-fit: contain;
  /* position: absolute;
  top: -30px;
  left: 0; */
}

#preview_description {
  display: flex;
  flex-direction: column;
  align-items: flex-start;

  padding-inline: 20px;

  width: min(350px, 45%);
}

#preview_description > p {
  padding-block: 10px;
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

#design_submit_button {
  padding-block: 10px;
  padding-inline: 30px;
  color: white;
  background-color: #233150;

  border: none;
  border-radius: 10px;
  outline: none;
  cursor: pointer;

  font-size: medium;
  font-family: sans-serif;
}

@media (max-width: 1054px) {
  #admin_page {
    flex-direction: column;
    padding-block-start: 70px;
  }

  #preview {
    width: 90%;
  }

  #preview_image_h1 {
    width: 100%;
  }

  #preview_image {
    width: 100%;
  }

  #preview_description {
    width: 100%;
  }

  #preview_description > h1 {
    text-align: center;
  }

  #inputs {
    width: 100%;
    height: fit-content;
    padding-block: 40px;
    align-items: center;
  }
}

@media (max-width: 600px) {
  #preview {
    width: 90%;
    height: fit-content;
    flex-direction: column;
    padding: 0;
  }
  #admin_page {
    padding-block-start: 0;
  }

  #preview_image {
    width: 100%;
    /* height: fit-content;
    line-height-step: 500px; */
  }

  /* #preview_image > img {
    top: 70px;
  } */

  #preview_description {
    text-align: center;
  }

  #preview_description > h1 {
    padding-block-end: 10px;
    margin-inline: auto;
  }
}
