#design_detail {
  padding-block: 50px;
  padding-inline: 50px;
}

#main_design_detail {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  height: max-content;
  width: 70%;
  margin: auto;
  box-shadow: 3px 3px 10px 3px gray;
  border-radius: 10px 10px;

  position: relative;
}

#detail_page_col1,
#detail_page_col2 {
  height: fit-content;
}

#detail_page_col1 {
  width: fit-content;
}

#detail_page_col2 {
  width: 45%;

  padding-inline: 30px;
  padding-block-start: 40px;
}

#detail_page_image {
  width: fit-content;
  height: fit-content;
  cursor: pointer;
}

#detail_page_image > img {
  width: 100%;
  height: 100%;
  border-radius: 10px 10px 10px 10px;
  object-fit: contain;
}

#detail_page_col2 > p {
  padding-block-start: 20px;
  width: min(400px, 100%);
  font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto,
    Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

#fullImageDiv {
  display: none;
  position: absolute;
  top: 0;
  left: 0;
  height: 140%;
  width: 100%;
  background-color: rgba(0, 0, 0, 0.7);
  color: white;
}

#fullImage_container {
  margin-inline: auto;
  margin-block-start: 7vmin;
  width: fit-content;
  height: 90vmin;
  color: white;
  display: flex;
  align-items: center;
  justify-content: center;
}

#fullImage {
  height: 100%;
  width: 100%;
  margin-block: 10px;
  object-fit: cover;
  border: 2px solid white;
  background-color: black;
  object-fit: contain;
}

#fullImageDiv > svg {
  position: absolute;
  top: 30px;
  right: 20px;
  color: white;
  cursor: pointer;
  z-index: 1000;
}

#delete_update_icons_design_detail {
  position: absolute;
  top: 10px;
  right: 5px;
  background-color: rgb(78 78 78);
}
/* linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)) */
#delete_update_icons_design_detail > svg {
  color: white;
  margin-inline: 3px;
  z-index: 1000;
  cursor: pointer;
}

@media (max-width: 1000px) {
  #design_detail {
    padding-inline: 30px;
  }

  #main_design_detail {
    /* flex-direction: column; */
    width: 99%;
    padding-inline: 0;
    padding-block-start: 0;
    padding-block-end: 0;
  }

  #detail_page_col1 {
    width: 100%;
    /* overflow: hidden; */
    height: fit-content;
  }

  #detail_page_image {
    width: 100%;
  }

  #detail_page_image > img {
    border-radius: 10px 10px 10px 10px;
  }

  #detail_page_col2 {
    width: 100%;
    height: fit-content;
    /* text-align: center; */
    padding-block-start: 20px;
  }

  #detail_page_col2 > p {
    width: 100%;
  }

  /* #fullImage {
    height: fit-content;
    width: fit-content;
    margin-block: 10px;
    object-fit: contain;
    border: 2px solid white;
  } */
}

@media (max-width: 600px) {
  #design_detail {
    padding-block: 20px;
    padding-inline: 30px;
  }

  #detail_page_col2 {
    text-align: center;
  }
  #main_design_detail {
    width: 100%;
    height: 100%;
    flex-direction: column;
  }
  #fullImage_container {
    position: fixed;
    margin-block-start: 90px;
    margin-inline-start: 5%;
  }
}
