#card {
  /* box-shadow: 2px 2px 5px gray; */
  border-radius: 10px 10px 0 0;
  width: 300px;
  overflow: hidden;
  position: relative;
}

#delete_update_icons_design_card {
  position: absolute;
  top: 10px;
  right: 5px;
  background-color: rgb(78 78 78);
}
/* linear-gradient(rgba(0, 0, 0, 0.7), rgba(0, 0, 0, 0.7)) */
#delete_update_icons_design_card > svg {
  color: white;
  margin-inline: 3px;
  z-index: 1000;
  cursor: pointer;
}

#card_content {
  text-align: center;
  padding-block: 20px;
}

#card_content > h1 {
  color: rgb(19, 17, 17);
  font-size: 20px;
}

@media (max-width: 550px) {
  #card {
    width: 90vw;
  }
}
